* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}

ul,
ol {
  list-style: none;
}
a {
  text-decoration: none;
}

body {
  background-color: #1a1400;
  overflow-x: hidden;
  width: 100%;
  height: 100%;
}
/*desain navbar*/

.container {
  margin-left: 50px;
  margin-right: 50px;
}
.nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 80px;
  padding-top: 20px;
}

.nav-brand-mobile {
  display: none;
  color: #ffffff;
}
.nav-brand {
  font-size: 2.5em;
  cursor: pointer;
  color: #ffffff;
}

.nav ul {
  width: 100%;
  display: grid;
  grid-template-columns: 100px 100px 120px;
  justify-content: right;
  gap: 1rem;

}
.nav a {
  padding: 10px;
  font-size: 1.5em;
  font-weight: 500;
  color: #ffffff;
  transition: 0.4s ease-in;
  margin-left: 25px;
}
.nav a:hover {
  text-decoration: underline;
}

.nav-brand:hover,
.nav a:hover {
  opacity: 0.5;
  transition: 0.4s ease-in;
}
a, button, input, textarea {
  display: inline-block;
  min-width: 44px;
  min-height: 44px;
}
