  z-index: 99;
  position: relative;
  top: 16px;
  right: 35px;
  min-width: 44px;
  min-height: 44px;
}

.menu-tombol div {
  width: 25px;
  height: 2px;
  background-color: white;
  margin: 7px;
  transition: all 0.3s;
  z-index: 99;
}

/*desain header*/
.header {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: cover;
  background-position: center;
  
}
.trans-header{
  background-color: #1a14007d;
  margin-top: 20px;
  min-height: 550px;
  display: grid;
  place-content: center;
  text-align: center;
  width: 100%;
}
.header-inner h1 {
  padding: 20px;
  font-size: 4.5em;
  font-weight: 600;
  background-color: #ffcc00;
  color: #1a1400;
}
.header-inner h3 {
  font-size: 3em;
  color: #ffffff;
}
.header-inner p {
  font-size: 30px;
  color: #ffffff;
}

/*Desain main*/
.headline-title {
  text-align: center;
  padding: 30px;
  font-size: 2.5em;
  color: #ffffff;
}

.post {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(600px, 1fr));
  gap: 50px;
}
.post-item {
  display: inline-block;
  background-color: #e6b800;
  color: #fffae6;
  box-sizing: border-box;
  overflow: visible;
}

.post-item-title {
  position: absolute;
  padding: 10px;
  color: #fffae6;
  font-size: 1.5em;
  background-color: #e6b800;
}

.post img {
  display: inline-block;
  width: 100%;
  height: 450px;
}
.post-item-description {
  padding: 20px;
}

.post-item-description h4 {
  font-size: 1.5em;
  padding-bottom: 20px;
}
.post-item-description h3 {
  font-size: 1.7em;
  padding-bottom: 10px;
}
.post-item-description p {
  font-size: 18px;
}

/* membuat footer */
footer {
  margin-top: 40px;
  background-color: #ffcc00;
}

.title-footer {
  text-align: center;
  padding: 2.5em;
  font-size: 20px;
}

/* membuat fokus */
div[tabindex="0"]:focus {
  outline: 4px solid black;
}
/*membuat skip content*/
.skip-link {
  position: absolute;
  top: -70px;
  left: 0;
  background-color: #ffcc00;
  color: #ffffff;
  padding: 8px;
  z-index: 100;
}

.skip-link:focus {
  top: 0;
}

