 font-size: 1.7em;\n  padding-bottom: 10px;\n}\n.post-item-description p {\n  font-size: 18px;\n}\n\n/* membuat footer */\nfooter {\n  margin-top: 40px;\n  background-color: #ffcc00;\n}\n\n.title-footer {\n  text-align: center;\n  padding: 2.5em;\n  font-size: 20px;\n}\n\n/* membuat fokus */\ndiv[tabindex=\"0\"]:focus {\n  outline: 4px solid black;\n}\n/*membuat skip content*/\n.skip-link {\n  position: absolute;\n  top: -70px;\n  left: 0;\n  background-color: #ffcc00;\n  color: #ffffff;\n  padding: 8px;\n  z-index: 100;\n}\n\n.skip-link:focus {\n  top: 0;\n}\n\n/* Responsive */\n\n/* Small devices (landscape phones, 576px and up) */\n@media (max-width: 680px) {\n  .menu-tombol {\n    display: inline-block;\n    cursor: pointer;\n  }\n\n  .toggle-burger .line1 .line2 .line3 {\n  }\n\n  /*    nav*/\n  nav {\n    position: fixed;\n    width: 100%;\n    right: 0;\n    top: -100%;\n    height: 100vh;\n    flex-direction: column;\n    background: #1a1400;\n    border-left: 1px solid #dfdcdc;\n    z-index: 9;\n    transition: .4s ease;\n  }\n\n  nav ul {\n    display: flex;\n    height: 100vh;\n    align-items: center;\n    flex-direction: column;\n    justify-content: center;\n  }\n\n  .nav-active {\n    top: 0;\n  }\n}\n\n/* Medium devices (tablets, 768px and up) */\n\n@media (max-width: 768px) {\n}\n\n/* Large devices (desktops, 992px and up) */\n\n@media (max-width: 992px) {\n  .nav-list {\n    width: 70%;\n  }\n}\n\n/* Extra large devices (large desktops, 1200px and up) */\n#wartegList, #wartegs {\n  margin: 32px auto auto;\n  text-align: left;\n  justify-content: center;\n}\n@media screen and (min-width: 550px) {\n\n  /*    card*/\n  #wartegList , #wartegs {\n    display: grid;\n    grid-template-columns: repeat(2, 50%);\n    gap: 20px;\n  }\n}\n@media screen and (min-width: 900px) {\n\n  #wartegList , #wartegs{\n    display: grid;\n    grid-template-columns: repeat(3, 30%);\n    gap: 29px;\n  }\n}\n@media screen and (min-width: 1200px) {\n  #wartegList , #wartegs{\